/* eslint no-undef: 0 */
/* eslint arrow-parens: 0 */
import React from 'react';
import { enquireScreen } from 'enquire-js';

import Banner0 from './Banner0';
import Feature1 from './Feature1';
import Feature2 from './Feature2';
import Feature0 from './Feature0';

import {
  Banner00DataSource,
  Feature10DataSource,
  Feature20DataSource,
  Feature11DataSource,
  Feature00DataSource,
} from './data.source';
import './less/antMotionStyle.less';

let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

// const { location } = window;

export default class Page2 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
      // show: !location.port, //  dva 2.0 
    };
  }

  componentDidMount() {
    // ;
    enquireScreen((b) => {
      this.setState({ isMobile: !!b });
    });
    // dva 2.0 
    /*  dva 2.0  start */
    // if (location.port) {
    //   //  build  200-300ms ;
    //   setTimeout(() => {
    //     this.setState({
    //       show: true,
    //     });
    //   }, 500);
    // }
    /*  dva 2.0  end */
  }

  render() {
    const children = [
      <Banner0
        id="Banner0_0"
        key="Banner0_0"
        dataSource={Banner00DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature1
        id="Feature1_0"
        key="Feature1_0"
        dataSource={Feature10DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature2
        id="Feature2_0"
        key="Feature2_0"
        dataSource={Feature20DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature1
        id="Feature1_1"
        key="Feature1_1"
        dataSource={Feature11DataSource}
        isMobile={this.state.isMobile}
      />,
      <Feature0
        id="Feature0_0"
        key="Feature0_0"
        dataSource={Feature00DataSource}
        isMobile={this.state.isMobile}
      />,
    ];
    return (
      <div
        className="templates-wrapper"
        ref={(d) => {
          this.dom = d;
        }}
      >
        {/*  dva 2.0  {children} start */}
        {/* {this.state.show && children} */}
        {children}
        {/*  dva 2.0  {children} end */}
      </div>
    );
  }
}
